<template>
    <div class="add-class-wrapper">
         <div class="add-class-box">
            <div class="search">
                <span></span>
                <div>
                   <div style="display inline-block; margin-left: 1vw">
                        <el-input v-model="className" placeholder="" @input="searchData" clearable></el-input>
                   </div>
                </div>
                <el-button type='primary' @click="addClass('add')" class="add-ident"></el-button>
            </div>
            <div class="table">
                <p class="table-title"></p>
                <el-table :data="myTableData" style="width: 100%">
                    <el-table-column prop="id" label="" min-width="5px"></el-table-column>
                    <el-table-column prop="name" label="" min-width="15px"></el-table-column>
                    <el-table-column prop="time" label="" min-width="15px"></el-table-column>
                    <el-table-column label="" min-width="60px">
                        <template slot-scope="scope">
                            <el-button type="text" size="small" @click="addClass('redact',scope.row,scope.$index)"><i class="iconfont">&#xe64b;</i></el-button>
                            <el-button type="text" size="small" @click="setZhengshi(scope.row)"><i class="iconfont">&#xe64b;</i></el-button>
                            <el-button type="text" size="small" @click="setMoni(scope.row)"><i class="iconfont">&#xe64b;</i></el-button>
                            <el-button type="text" size="small" @click="seeStudent(scope.row)"><i class="iconfont">&#xe738;</i></el-button>
                            <el-button type="text" size="small" @click="seeExamRecord(scope.row)"><i class="iconfont">&#xe738;</i></el-button>
                            <el-button type="text" size="small" @click="deleteData(scope.row)"><i class="iconfont">&#xe631;</i></el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </div>
        </div>
        <div>
            <el-dialog :title="disTitle" :visible.sync="dialogFormVisible" :close-on-click-modal='false'>
                <el-form :model="formData">
                    <div v-if="isClass">
                        <el-form-item label="" :label-width="isClass ? '100px' : '200px'" v-model="formData.name">
                            <el-input v-model="formData.name" placeholder="" autocomplete="off"></el-input>
                        </el-form-item>
                    </div>
                    <div v-else>
                        <div v-if="disTitle == ''">
                            <el-form-item label="" label-width="200px" v-model="formData.examination">
                                <el-select v-model="formData.examination" filterable placeholder="" @change='changeExamination'>
                                    <el-option v-for="item in examinations" :key="item.title" :label="item.title" :value="item.id">
                                    </el-option>
                                </el-select>
                                <el-select style="marign-left:3vw" v-model="formData.theory" filterable placeholder="" @change='changeExaminationLilun'>
                                    <el-option v-for="item in examinationsLilun" :key="item.title" :label="item.title" :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="" label-width="200px">
                                <el-date-picker v-model="formData.timea" type="datetime" placeholder=""></el-date-picker>
                            </el-form-item>
                            <el-form-item label="()" label-width="200px">
                                <el-input v-model="formData.sca" autocomplete="off" placeholder=""></el-input>
                            </el-form-item>
                            <el-form-item label="" label-width="200px">
                                <el-date-picker v-model="formData.timeb" type="datetime" placeholder=""></el-date-picker>
                            </el-form-item>
                            <el-form-item label="()" label-width="200px">
                                <el-input v-model="formData.scb" autocomplete="off" placeholder=""></el-input>
                            </el-form-item>
                            <template>
                                <el-form-item label="" label-width="200px">
                                    <el-radio-group v-model="meanwhile">
                                        <el-radio :label="0"></el-radio>
                                        <el-radio :label="1"></el-radio>
                                    </el-radio-group>
                                </el-form-item>
                            </template>
                        </div>
                        <div v-if="disTitle == ''">
                            <el-form-item label="" label-width="200px" v-model="formData.examination_mn">
                                <el-select v-model="formData.examination_mn" filterable placeholder=""  @change='changeExaminationMn'>
                                    <el-option v-for="item in examination_mns" :key="item.title" :label="item.title" :value="item.id">
                                    </el-option>
                                </el-select>
                                <el-select v-model="formData.theory_mn" filterable placeholder=""  @change='changeExaminationMnLilun'>
                                    <el-option v-for="item in examination_mnsLilun" :key="item.title" :label="item.title" :value="item.id">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="" label-width="200px">
                                <el-date-picker v-model="formData.timea" type="datetime" placeholder=""></el-date-picker>
                            </el-form-item>
                            <el-form-item label="()" label-width="200px">
                                <el-input v-model="formData.sca" autocomplete="off" placeholder=""></el-input>
                            </el-form-item>
                            <el-form-item label="" label-width="200px">
                                <el-date-picker v-model="formData.timeb" type="datetime" placeholder=""></el-date-picker>
                            </el-form-item>
                            <el-form-item label="()" label-width="200px">
                                <el-input v-model="formData.scb" autocomplete="off" placeholder=""></el-input>
                            </el-form-item>
                            <template>
                                <el-form-item label="" label-width="200px">
                                    <el-radio-group v-model="meanwhile">
                                        <el-radio :label="0"></el-radio>
                                        <el-radio :label="1"></el-radio>
                                    </el-radio-group>
                                </el-form-item>
                            </template>
                        </div>
                    </div>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false"> </el-button>
                    <el-button type="primary" @click="setClassTrue"> </el-button>
                </div>
            </el-dialog>
        </div>
        <el-dialog title="" :visible.sync="dialogVisible2" width="30%">
            <span>?</span>
            <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible2 = false"> </el-button>
                <el-button type="primary" @click="deleteTrue"> </el-button>
            </span>
        </el-dialog>
    </div>
</template>

<style scoped>
    @import './addClass.css';
</style>

<script>
export default {
    data(){
        return {
            value:'',
            id: 0,
            isClass: true,
            meanwhile: 0,
            className: '',
            disTitle: '',
            dialogFormVisible: false,
            dialogVisible2: false,
            tableData: [],
            myTableData: [],
            examinations: [],
            examination_mns: [],
            examinationsLilun: [],
            examination_mnsLilun: [],
            jinengID: '',
            lilunID: '',
            formData:{
                examination: '',
                theory: '',
                examination_mn: '',
                theory_mn: '',
                name: '',
                timea: '',
                timeb: '',
                sca: '',
                scb: ''
            }
        }
    },
    methods: {
        // /
        addClass(value,row){
            this.isClass = true;
            if(value == 'add'){
                this.disTitle = '';
                this.formData = { examination: '', examination_mn: '', name: ''};
                this.zhengshiID = 0;
                this.moniID = 0;
                this.dialogFormVisible = true;
            }else{
                this.id = row.id;
                this.axios.get('/admin/user/getClassId?id='+row.id).then(res =>{
                    this.disTitle = '';
                    this.formData = res.data.data;
                    this.dialogFormVisible = true;
                })
            }
        },
        // /
        setClassTrue(){
            const data = {};
            if(this.disTitle == ''){
                if(!this.formData.name) return this.$message({message: '',type: 'error'})
                data.name = this.formData.name;
                this.axios.post('/admin/user/addClass',data).then(res =>{
                    this.$message({
                        message: res.data.msg,
                        type: 'success'
                    })
                    this._getInfoData();
                    this.dialogFormVisible = false;
                })
            }else if(this.disTitle == ''){
                if(!this.formData.name) return this.$message({message: '',type: 'error'})
                data.id = this.id;
                data.name = this.formData.name;
                this.axios.post('/admin/user/modifyClass',data).then(res =>{
                    this.$message({
                        message: res.data.msg,
                        type: 'success'
                    })
                    this._getInfoData()
                    this.dialogFormVisible = false;
                })
            }else if(this.disTitle == ''){
                // const data = this.deepClone(this.formData) //
                data.b_id = this.id;
                data.j_id = this.jinengID;
                data.timea = this.formData.timea;
                data.sca = this.formData.sca;
                data.l_id = this.lilunID;
                data.timeb = this.formData.timeb;
                data.scb = this.formData.scb;
                data.zs = 1;
                data.meanwhile = this.meanwhile;
                this.axios.post('/admin/user/add_class_an', data).then(res =>{
                    console.log(res);
                    this.$message({
                        message: res.data.msg,
                        type: 'success'
                    })
                    this.dialogFormVisible = false;
                })
            }else if(this.disTitle == ''){
                data.b_id = this.id;
                data.j_id = this.jinengID;
                data.timea = this.formData.timea;
                data.sca = this.formData.sca;
                data.l_id = this.lilunID;
                data.timeb = this.formData.timeb;
                data.scb = this.formData.scb;
                data.zs = 2;
                data.meanwhile = this.meanwhile;
                this.axios.post('/admin/user/add_class_an', data).then(res =>{
                    console.log(res)
                    this.$message({
                        message: res.data.msg,
                        type: 'success'
                    })
                    this.dialogFormVisible = false;
                })
            }
        },
        setZhengshi(row){
            this.id = row.id;
            this.isClass = false;
            this.isZhenshi = true;
            this.disTitle = ''
            this.dialogFormVisible = true
        },
        setMoni(row){
            this.id = row.id;
            this.isClass = false;
            this.disTitle = ''
            this.dialogFormVisible = true
        },
        // 
        deleteData(row){
            this.id = row.id;
            this.dialogVisible2 = true;
        },
        // 
        deleteTrue(){
            this.axios.get('/admin/user/deleteClass?id='+this.id).then(res =>{
                this.$message({
                        message: res.data.msg,
                        type: 'success'
                    })
                this.dialogVisible2 = false;
                this._getInfoData()
            })
        },
        // 
        seeStudent(row){
            this.$emit('seeStudent',row.id)
        },
        seeExamRecord(row){
            this.$emit('seeExamRecord',row.id)
        },
        changeExamination(e){
            this.jinengID = e;
        },
        changeExaminationMn(e){
            this.jinengID = e;
        },
        changeExaminationLilun(e){
            this.lilunID = e;
        },
        changeExaminationMnLilun(e){
            this.lilunID = e;
        },
        // 
        searchData(value){
            this.myTableData = this.tableData.filter(item =>{
                return item.name.match(value)
            })
        },
        _getInfoData(){
            this.axios.get('/admin/user/classList').then(res =>{
                this.tableData = res.data.data;
                this.tableData.forEach((ele,index) =>{
                    ele.index = index+1
                })
                this.myTableData = this.tableData;
            })
            // 
            this.axios.get('/admin/user/getIssueZheng').then(res =>{
                this.examinations = res.data.data;
            })
            this.axios.get('/admin/user/getIssueMu').then(res =>{
                this.examination_mns = res.data.data
            })
            // 
            this.axios.get('/admin/user/getPaperZheng').then(res =>{
                this.examinationsLilun = res.data.data;
            })
            this.axios.get('/admin/user/getPaperMu').then(res =>{
                this.examination_mnsLilun = res.data.data
            })
        }
    },
    created(){
        this._getInfoData()
    }
}
</script>